* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
}

body {
  padding: 50px;
}

input {
  font-size: 1.2rem;
  color: grey;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 10px;
  overflow: hidden;
  margin-top: 10px;
  margin-right: 10px;
}

.card p {
  text-align: center;
  padding: 5px;
}

.card img {
  max-height: 500px;
  max-width: 100%;
}